@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300;400;700&display=swap);
body, html {
  /* background-image: url('./bg2.jpg');
  background-size: cover;
  background-repeat: no-repeat; */
  /* background: linear-gradient(0deg, rgba(67,0,161,1) 0%, rgba(9,9,121,1) 23%, rgba(0,212,255,1) 100%); */
  width: 100%;
  margin: 0 auto;
  font-family: 'MuseoModerno', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  height: 100vh;
  background: linear-gradient(#10416b, rgb(34, 2, 53)); 
}

#mainContainer {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  background-image: url(../../static/media/bg.9836b0bb.jpg);
  background-size: cover;
  width: 85%;
  max-width: 45vh;
  margin: 0 auto;
  border-radius: 3%;
  border: 3px solid white;
  background-color: skyblue;

}

#root {
  font-family: 'MuseoModerno', cursive;
  padding: 10px;
  margin: 3% 0 auto;
  overflow: hidden;
}

#container {
  color: white;
  padding: 10px;
  text-align: center;
  overflow: hidden;
}


#searchForm, input {
  font-family: 'MuseoModerno', cursive;
  margin: 0 auto;
  display: inline-block;
  outline: none;
  border-radius: 30px;

}

#input {
  padding: 3px;
  outline: none;
  border: none;
  text-align: center;
}

#submit {
  color: white;
  font-family: 'MuseoModerno', cursive;
  font-weight: 600;
  background: none;
  outline: none;
  border: 1px solid white;
  padding: 5px 3vw;
  border-radius: 20px;
  margin: 20px;
}

#submit:hover {
  background-color: rgba(0, 0, 0, 0.301);
}

#charts {
  font-size: calc(1.2em + 1.5vw);
  padding: 20px;
  margin: 0 auto;
  text-align: center;
  overflow: hidden;
  border-radius: 3%;
}

#chartContainer {
  max-width: 55%;
  margin: 0 auto;
  padding: 10px;
}


 #chartInfo p{
  max-width: 30vh;
  background-color: whitesmoke;
  font-size: 12px;
  padding: 0 auto;
  margin: 0 auto 10px;
}

#temperature {
  font-size: inherit;
  color: white;
  margin: 0px auto;
  text-shadow: 3px 3px rgba(0, 0, 0, 0.219);
}

#weatherIcon {
  margin-bottom: -12px;
}

#cityName {
  font-size: inherit;
  margin: -10px 0 2vh auto;
  padding: 0 auto;
  /* text-shadow: 3px 3px rgba(0, 0, 0, 0.219); */
}

